import { useMemo, useEffect } from 'react';
import { getPagesArray } from '../components/utils/pages'
import cx from "classnames";

export const usePages = (totalPages, page, setPage, cls) => {
	useEffect(() => {
		setPage(1);
	}, [totalPages]);
	
	const pages = useMemo(() => {
		totalPages = Math.ceil(totalPages);
		const pagesArray = getPagesArray(totalPages, page);
		
		if (!totalPages) {
			return null;
		}
		return(
			<div className={cx("pages-container", cls)}>
				<div className="pages">
					<button className="pages-btn-back_full pages-btn pages-btn_full btn btn_plain" disabled={page <= 1}
						onClick={() => setPage(1)}>
						<div className="pages-btn__icon-container">
							<svg xmlns="http://www.w3.org/2000/svg" width="7" height="12" viewBox="0 0 7 12" fill="none">
								<path d="M6 11L1 6L6 1" stroke="#384263"/>
							</svg>
							<svg xmlns="http://www.w3.org/2000/svg" width="7" height="12" viewBox="0 0 7 12" fill="none">
								<path d="M6 11L1 6L6 1" stroke="#384263"/>
							</svg>
						</div>
					</button>
					<button className="pages-btn-back pages-btn btn btn_plain" disabled={page <= 1}
						onClick={() => setPage(page - 1)}>
						<div className="pages-btn__icon-container">
							<svg xmlns="http://www.w3.org/2000/svg" width="7" height="12" viewBox="0 0 7 12" fill="none">
								<path d="M6 11L1 6L6 1" stroke="#384263"/>
							</svg>
						</div>
						
					</button>
					{pagesArray.map((p, i) => {
						return <button className={cx('page btn btn_plain', page === p && 'current')}
							key={p}
							onClick={() => {
								setPage(p)
							}}>
								{p}
							</button>
					})}
					<button className="pages-btn-forward pages-btn btn btn_plain" disabled={page >= totalPages}
						onClick={() => setPage(page + 1)}>
						<div className="pages-btn__icon-container">
							<svg xmlns="http://www.w3.org/2000/svg" width="7" height="12" viewBox="0 0 7 12" fill="none">
								<path d="M1 1L6 6L1 11" stroke="#6988FF"/>
							</svg>
						</div>
						
					</button>
					<button className="pages-btn-forward_full pages-btn pages-btn_full btn btn_plain" disabled={page >= totalPages}
						onClick={() => setPage(totalPages)}>
						<div className="pages-btn__icon-container">
							<svg xmlns="http://www.w3.org/2000/svg" width="7" height="12" viewBox="0 0 7 12" fill="none">
								<path d="M1 1L6 6L1 11" stroke="#6988FF"/>
							</svg>
							<svg xmlns="http://www.w3.org/2000/svg" width="7" height="12" viewBox="0 0 7 12" fill="none">
								<path d="M1 1L6 6L1 11" stroke="#6988FF"/>
							</svg>
						</div>
					</button>
				</div>
			</div>
		)
	}, [totalPages, page])

	return pages;
}