import React, { useEffect, useMemo, useState } from 'react';
import { ALL_MARKETS, OPTION_LIST_FRONTIER } from './../../../../components/utils/constants';
import useSort from '../../../../hooks/useSort';
import { ARBITRUM, useLocalStorageSerializeKey } from '../../../gmx-test/lib/legacy';
import TxHistoryItem from './TxHistoryItem';
import Loader from '../../../../components/UI/loader/Loader';
import Filter from '../../../../components/UI/Filter/Filter';
import { usePages } from '../../../../hooks/usePages';

const TxHistory = (props) => {
	const {
		txData,
		isLoading,
	} = props;
	const [markets, setMarkets] = useLocalStorageSerializeKey([ARBITRUM, "Tx-Markets"], ALL_MARKETS.slice());
	const [sortTxs, sortedTxs, setSortedTxs, sortType] = useSort(txData, markets, "market");
	useEffect(() => {
		const [param, dir] = sortType.split('__');
		sortTxs(param, dir, true);
	}, [markets]);
	useEffect(() => {
		if (txData.length) {
			sortTxs('block_timestamp', 1, true);
		} else {
			setSortedTxs([]);
		}
	}, [txData.length]);

	// Pages
	const [page, setPage] = useState(1);
	const totalPages = sortedTxs.length / OPTION_LIST_FRONTIER;
	const pages = usePages(totalPages, page, setPage, "pages-container_in-table");
	const pagedTxs = useMemo(() => {
		return sortedTxs.slice((page-1) * OPTION_LIST_FRONTIER, page * OPTION_LIST_FRONTIER);
	}, [page, sortedTxs]);

	const renderHead = () => {
		return (
			<tr className="Exchange-list-header">
				<th>
					Market
				</th>
				<th>
					<button
						className='btn_table-sort'
						onClick={() => {
							sortTxs('block_timestamp');
						}}
					>
						Date
					</button>
				</th>
				<th>
					Type
				</th>
				<th>
					<button
						className='btn_table-sort'
						onClick={() => {
							sortTxs('amount');
						}}
					>
						Amount
					</button>
				</th>
				<th>
					<button
						className='btn_table-sort'
						onClick={() => {
							sortTxs('amount_usd');
						}}
					>
						Amount, $
					</button>
				</th>
			</tr>
		)
	}
	return (
		<div className="TxHistory">
			<div className="TxHistory__header block__header">
				<h1 className="TxHistory__title">
					Transaction History
				</h1>
				<Filter
					items={ALL_MARKETS}
					selectedItems={markets}
					setSelectedItems={setMarkets}
					iconLabels={{
						GMX: "GMX_full",
						GNS: "GNS_full",
					}}
				/>
			</div>
			<div className="table-wrapper table-wrapper_scrollable">
				<table className="Exchange-list large App-box table_even">
					{renderHead()}
					<tbody>
						{(!sortedTxs.length) ?
							<tr>
								<td colSpan={8}>
									{
										isLoading ?
										<Loader /> :
											(txData.length 
												? "Please select markets from the list above"
												: "Please choose the desired amount and period to view the available options")
									}
								</td>
							</tr>
							:
							(isLoading
								? <tr>
										<td colSpan={8}>
											<Loader />
										</td>
									</tr>
								: pagedTxs.map(tx => {
										return(
											<TxHistoryItem
												key={`${tx.tx_hash}__${tx.asset}`}
												tx={tx}
											/>
										)
									}))
						}
					</tbody>
				</table>
			</div>
			{totalPages > 1 && (
				pages
			)}
		</div>
	);
};

export default TxHistory;