import React, { useEffect, useState } from 'react';
import './UserProfile.scss';
import UserGraph from './components/UserGraph';
import TxHistory from './components/TxHistory';
import { FLIPSIDE_API, getSqlQueries } from './flipsideConstants';
import { Flipside } from '@flipsidecrypto/sdk';

const UserProfile = ({ walletAddress }) => {
	const [txData, setTxData] = useState([]);
	const [account, setAccount] = useState("");
	const [isLoading, setIsLoading] = useState(true);
	
	// Setting title
	useEffect(() => {
		document.title = "User Profile | DeDeLend";
	}, []);

	// Fetching user tx data
	useEffect(() => {
		// Setting default account address
		if (walletAddress) {
			setTxData([]);
			setAccount(walletAddress);
		}
	}, [walletAddress]);
	
	let keys = [];
	useEffect(() => {
		if (account) {
			// Running SQL-queries
			const flipside = new Flipside(
				FLIPSIDE_API,
				"https://node-api.flipsidecrypto.com"
			);
			
			const queries = getSqlQueries(account);
			const promises = [];
			setIsLoading(true);
			for (const query of queries) {
				promises.push(
					flipside.query.run({ sql: query, ttlMinutes: 1})
						.then(res => {
							res.records?.forEach(record => {
								if (keys.find(key => key === record.tx_hash)) {
									return;
								}
								keys.push(record.tx_hash);
								txData.push(record);
								setIsLoading(false);
							})
						})
					);
			}
			Promise.all(promises)
				.then(() => {
					setTxData(txData.slice());
					setIsLoading(false);
				})
		}

		return () => { keys = [] };
	}, [account]);

	return (
		<div className="UserProfile">
			<UserGraph
				txData={txData}
				isLoading={isLoading}
			/>
			<TxHistory
				txData={txData}
				isLoading={isLoading}
			/>
		</div>
	);
};

export default UserProfile;