export const getPageCount = (totalCount, limit) => {
	return Math.ceil(totalCount / limit);
}

export const getPagesArray = (totalPages, page) => {
	const visibleFrontier = 3;
	const pagesArr = [];
	const startingPoint = page === 1
		? 1
		: page === totalPages 
			? totalPages - 2 
			: page - 1;
	
	for (let i = startingPoint || 1; i < Math.min(startingPoint + visibleFrontier, totalPages + 1); i++) {
		pagesArr.push(i);
	}

	return pagesArr;
}