import React, { useMemo } from 'react';
import './UserGraph.scss';
import cx from "classnames";
import { ImSpinner2 } from 'react-icons/im';

const UserGraph = (props) => {
	const {
		txData,
		isLoading,
	} = props;

	const [profitPerps, profitOptions, profitTotal] = useMemo(() => {
		if (txData.length) {
			const profitPerps = txData.reduce((profit, tx) => {
				if (tx.market === "GMX" || tx.market === "GNS") {
					return profit + tx.amount_usd;
				} else {
					return profit;
				}
			}, 0);
			const profitOptions = txData.reduce((profit, tx) => {
				if (tx.market === "Hegic" || tx.market === "Lyra") {
					return profit + tx.amount_usd;
				} else {
					return profit;
				}
			}, 0);
			const profitTotal = profitPerps + profitOptions;
	
			return [profitPerps, profitOptions, profitTotal];
		}
		else return [];
	}, [txData.length])
	
	const profitPerpsSign = profitPerps < 0 ? "" : "+";
	const profitOptionsSign = profitOptions < 0 ? "" : "+";
	const profitTotalSign = profitTotal < 0 ? "" : "+";

	return (
		<div className="UserGraph">
			<h1>History</h1>
			<div className="UserGraph__content">
				<div className="UserGraph__graph App-box">

				</div>
				<div className="UserGraph__overview App-box">
					<h2>Overview</h2>
					<div className="UserGraph__overview-items">
						<div className="UserGraph__overview-item">
							<div className="UserGraph__overview-item-title">
								Realized PnL (Perps)
							</div>
							<div className="UserGraph__overview-item-pnl">
								{isLoading
									? <ImSpinner2 className="spin position-loading-icon" />
									: (
											<>
												{profitPerpsSign}${Math.abs(profitPerps).toFixed(2)}
												<span
													className={cx({ 
														positive: profitPerps > 0,
														negative: profitPerps < 0,
														muted: profitPerps === 0,
													})}>
												</span>
											</>
									)}
								
							</div>
						</div>
						<div className="UserGraph__overview-item">
							<div className="UserGraph__overview-item-title">
								Realized PnL (Options)
							</div>
							<div className="UserGraph__overview-item-pnl">
								{isLoading
									? <ImSpinner2 className="spin position-loading-icon" />
									: (
											<>
												{profitOptionsSign}${Math.abs(profitOptions).toFixed(2)}
												<span
													className={cx({ 
														positive: profitOptions > 0,
														negative: profitOptions < 0,
														muted: profitOptions === 0,
													})}>
												</span>
											</>
									)}
								
							</div>
						</div>
						<div className="UserGraph__overview-item">
							<div className="UserGraph__overview-item-title">
								Total Realized PnL
							</div>
							<div className="UserGraph__overview-item-pnl">
								{isLoading
									? <ImSpinner2 className="spin position-loading-icon" />
									: (
											<>
												{profitTotalSign}${Math.abs(profitTotal).toFixed(2)}
												<span
													className={cx({ 
														positive: profitTotal > 0,
														negative: profitTotal < 0,
														muted: profitTotal === 0,
													})}>
												</span>
											</>
									)}
								
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	);
};

export default UserGraph;