import { format } from 'date-fns';
import React from 'react';
import { DATE_FORMAT_TEMPLATE } from '../../../../components/utils/constants';
import cx from "classnames";

const TxHistoryItem = (props) => {
	const {
		tx
	} = props;
	// console.log(tx);
	
	const typeLabel = tx.transaction_type === "Received"
		? "Inflow"
		: "Outflow";
	const isNegative = tx.amount_usd < 0;
	const amountSign = isNegative ? "" : "+";

	const marketLogo = require(`../../../../img/icon-${tx.market}_full.svg`).default;
	return (
		<tr>
			<td>
				<img src={marketLogo} alt={`${tx.market} logo`} />
			</td>
			<td>
				{format(new Date(tx.block_timestamp), DATE_FORMAT_TEMPLATE)}
			</td>
			<td>
				<a href={tx.tx_hash} target='_blank' rel="noreferrer">
					{typeLabel}
					<svg
						style={{ marginLeft: 5, transform: "translateY(1px)" }}
						width="12" height="12" viewBox="0 0 20 20" fill="none" xmlns="http://www.w3.org/2000/svg"
					>
						<path d="M11.25 8.75L17.5 2.5M13.3333 2.5H17.5V6.66667M17.5 11.6667V15.8333C17.5 16.2754 17.3244 16.6993 17.0118 17.0118C16.6993 17.3244 16.2754 17.5 15.8333 17.5H4.16667C3.72464 17.5 3.30072 17.3244 2.98816 17.0118C2.67559 16.6993 2.5 16.2754 2.5 15.8333V4.16667C2.5 3.72464 2.67559 3.30072 2.98816 2.98816C3.30072 2.67559 3.72464 2.5 4.16667 2.5H8.33333" stroke="#747FA6" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
					</svg>
				</a>
			</td>
			<td>
				<div
					className={cx({
						positive: !isNegative,
						negative: isNegative,
					})}
				>
					{amountSign}{Math.abs(tx.amount)} {tx.asset}
				</div>
			</td>
			<td>
				<div
					className={cx({
						positive: !isNegative,
						negative: isNegative,
					})}
				>
					{amountSign}${Math.abs(tx.amount_usd).toFixed(2)}
				</div>
			</td>
		</tr>
	);
};

export default TxHistoryItem;