export const FLIPSIDE_API = "8c5a8a82-9b96-4fb5-9d9d-e5bd9ce8fe92";

export const getSqlQueries = (account) => {
	return [
		`WITH
		increase_position AS (
			SELECT
				CONCAT('https://arbiscan.io/tx/', TX_HASH) AS TX_HASH,
				BLOCK_TIMESTAMP,
				FROM_ADDRESS AS "User",
				RAW_AMOUNT,
				'GMX' AS "Market",
				contract_address,
				'Spent' AS "TransactionType",
				CONCAT(
					EXTRACT(MONTH, BLOCK_TIMESTAMP),
					'-',
					EXTRACT(DAY, BLOCK_TIMESTAMP),
					'-',
					EXTRACT(YEAR, BLOCK_TIMESTAMP)
				) AS "Timestamp"
			FROM
				arbitrum.core.fact_token_transfers
			WHERE
				-- arbitrum.core.fact_token_transfers.origin_function_signature = '0xf2ae372f'
				arbitrum.core.fact_token_transfers.contract_address IN (
					lower('0xFF970A61A04b1cA14834A43f5dE4533eBDDB5CC8'), -- USDC
					lower('0xda10009cbd5d07dd0cecc66161fc93d7c9000da1'), -- DAI
					lower('0xFd086bC7CD5C481DCC9C85ebE478A1C0b69FCbb9'), -- USDT
					lower('0x17FC002b466eEc40DaE837Fc4bE5c67993ddBd6F'), -- FRAX
					lower('0xFa7F8980b0f1E64A2062791cc3b0871572f1F7f0'), -- UNI
					lower('0x2f2a2543B76A4166549F7aaB2e75Bef0aefC5B0f'), -- WBTC
					lower('0xf97f4df75117a78c1A5a0DBb814Af92458539FB4'), -- LINK
					lower('0x82aF49447D8a07e3bd95BD0d56f35241523fBab1') -- WETH
				)
				AND to_address = lower('0xb87a436B93fFE9D75c5cFA7bAcFff96430b09868')
		),
		decrease_position AS (
			SELECT
				CONCAT('https://arbiscan.io/tx/', TX_HASH) AS TX_HASH,
				BLOCK_TIMESTAMP,
				TO_ADDRESS AS "User",
				RAW_AMOUNT,
				'GMX' AS "Market",
				contract_address,
				'Received' AS "TransactionType",
				CONCAT(
					EXTRACT(MONTH, BLOCK_TIMESTAMP),
					'-',
					EXTRACT(DAY, BLOCK_TIMESTAMP),
					'-',
					EXTRACT(YEAR, BLOCK_TIMESTAMP)
				) AS "Timestamp"
			FROM
				arbitrum.core.fact_token_transfers
			WHERE
				-- arbitrum.core.fact_token_transfers.origin_function_signature = '0xf2ae372f'
				arbitrum.core.fact_token_transfers.contract_address IN (
					lower('0xFF970A61A04b1cA14834A43f5dE4533eBDDB5CC8'), -- USDC
					lower('0xda10009cbd5d07dd0cecc66161fc93d7c9000da1'), -- DAI
					lower('0xFd086bC7CD5C481DCC9C85ebE478A1C0b69FCbb9'), -- USDT
					lower('0x17FC002b466eEc40DaE837Fc4bE5c67993ddBd6F'), -- FRAX
					lower('0xFa7F8980b0f1E64A2062791cc3b0871572f1F7f0'), -- UNI
					lower('0x2f2a2543B76A4166549F7aaB2e75Bef0aefC5B0f'), -- WBTC
					lower('0xf97f4df75117a78c1A5a0DBb814Af92458539FB4'), -- LINK
					lower('0x82aF49447D8a07e3bd95BD0d56f35241523fBab1') -- WETH
				)
				AND from_address = lower('0xb87a436B93fFE9D75c5cFA7bAcFff96430b09868')
				or from_address = lower('0x489ee077994b6658eafa855c308275ead8097c4a')
		),
		token_prices AS (
			SELECT
				CONCAT(
					EXTRACT(MONTH, HOUR),
					'-',
					EXTRACT(DAY, HOUR),
					'-',
					EXTRACT(YEAR, HOUR)
				) AS "Timestamp",
				HOUR,
				SYMBOL,
				PRICE,
				ROW_NUMBER() OVER (
					PARTITION BY
						SYMBOL,
						CONCAT(
							EXTRACT(MONTH, HOUR),
							'-',
							EXTRACT(DAY, HOUR),
							'-',
							EXTRACT(YEAR, HOUR)
						)
					ORDER BY
						HOUR DESC
				) AS rn
			FROM
				arbitrum.core.fact_hourly_token_prices
		),
		amounts AS (
			SELECT
				ip.TX_HASH,
				ip.BLOCK_TIMESTAMP,
				ip."User",
				ip."Market",
				ip.contract_address,
				CASE
					WHEN ip.contract_address = lower('0xFF970A61A04b1cA14834A43f5dE4533eBDDB5CC8') THEN - ip.RAW_AMOUNT / 1e6
					WHEN ip.contract_address = lower('0xda10009cbd5d07dd0cecc66161fc93d7c9000da1') THEN - ip.RAW_AMOUNT / 1e18
					WHEN ip.contract_address = lower('0xFd086bC7CD5C481DCC9C85ebE478A1C0b69FCbb9') THEN - ip.RAW_AMOUNT / 1e6
					WHEN ip.contract_address = lower('0x17FC002b466eEc40DaE837Fc4bE5c67993ddBd6F') THEN - ip.RAW_AMOUNT / 1e18
					WHEN ip.contract_address = lower('0xFa7F8980b0f1E64A2062791cc3b0871572f1F7f0') THEN - ip.RAW_AMOUNT / 1e18
					WHEN ip.contract_address = lower('0x2f2a2543B76A4166549F7aaB2e75Bef0aefC5B0f') THEN - ip.RAW_AMOUNT / 1e8
					WHEN ip.contract_address = lower('0xf97f4df75117a78c1A5a0DBb814Af92458539FB4') THEN - ip.RAW_AMOUNT / 1e18
					WHEN ip.contract_address = lower('0x82aF49447D8a07e3bd95BD0d56f35241523fBab1') THEN - ip.RAW_AMOUNT / 1e18
				END AS "Amount",
				ip."Timestamp",
				CASE
					WHEN ip.contract_address = lower('0xFF970A61A04b1cA14834A43f5dE4533eBDDB5CC8') THEN 'USDC'
					WHEN ip.contract_address = lower('0xda10009cbd5d07dd0cecc66161fc93d7c9000da1') THEN 'DAI'
					WHEN ip.contract_address = lower('0xFd086bC7CD5C481DCC9C85ebE478A1C0b69FCbb9') THEN 'USDT'
					WHEN ip.contract_address = lower('0x17FC002b466eEc40DaE837Fc4bE5c67993ddBd6F') THEN 'FRAX'
					WHEN ip.contract_address = lower('0xFa7F8980b0f1E64A2062791cc3b0871572f1F7f0') THEN 'UNI'
					WHEN ip.contract_address = lower('0x2f2a2543B76A4166549F7aaB2e75Bef0aefC5B0f') THEN 'WBTC'
					WHEN ip.contract_address = lower('0xf97f4df75117a78c1A5a0DBb814Af92458539FB4') THEN 'LINK'
					WHEN ip.contract_address = lower('0x82aF49447D8a07e3bd95BD0d56f35241523fBab1') THEN 'WETH'
				END AS "Asset",
				ip."TransactionType"
			FROM
				increase_position ip
			UNION
			SELECT
				dp.TX_HASH,
				dp.BLOCK_TIMESTAMP,
				dp."User",
				dp."Market",
				dp.contract_address,
				CASE
					WHEN dp.contract_address = lower('0xFF970A61A04b1cA14834A43f5dE4533eBDDB5CC8') THEN dp.RAW_AMOUNT / 1e6
					WHEN dp.contract_address = lower('0xda10009cbd5d07dd0cecc66161fc93d7c9000da1') THEN dp.RAW_AMOUNT / 1e18
					WHEN dp.contract_address = lower('0xFd086bC7CD5C481DCC9C85ebE478A1C0b69FCbb9') THEN dp.RAW_AMOUNT / 1e6
					WHEN dp.contract_address = lower('0x17FC002b466eEc40DaE837Fc4bE5c67993ddBd6F') THEN dp.RAW_AMOUNT / 1e18
					WHEN dp.contract_address = lower('0xFa7F8980b0f1E64A2062791cc3b0871572f1F7f0') THEN dp.RAW_AMOUNT / 1e18
					WHEN dp.contract_address = lower('0x2f2a2543B76A4166549F7aaB2e75Bef0aefC5B0f') THEN dp.RAW_AMOUNT / 1e8
					WHEN dp.contract_address = lower('0xf97f4df75117a78c1A5a0DBb814Af92458539FB4') THEN dp.RAW_AMOUNT / 1e18
					WHEN dp.contract_address = lower('0x82aF49447D8a07e3bd95BD0d56f35241523fBab1') THEN dp.RAW_AMOUNT / 1e18
				END AS "Amount",
				dp."Timestamp",
				CASE
					WHEN dp.contract_address = lower('0xFF970A61A04b1cA14834A43f5dE4533eBDDB5CC8') THEN 'USDC'
					WHEN dp.contract_address = lower('0xda10009cbd5d07dd0cecc66161fc93d7c9000da1') THEN 'DAI'
					WHEN dp.contract_address = lower('0xFd086bC7CD5C481DCC9C85ebE478A1C0b69FCbb9') THEN 'USDT'
					WHEN dp.contract_address = lower('0x17FC002b466eEc40DaE837Fc4bE5c67993ddBd6F') THEN 'FRAX'
					WHEN dp.contract_address = lower('0xFa7F8980b0f1E64A2062791cc3b0871572f1F7f0') THEN 'UNI'
					WHEN dp.contract_address = lower('0x2f2a2543B76A4166549F7aaB2e75Bef0aefC5B0f') THEN 'WBTC'
					WHEN dp.contract_address = lower('0xf97f4df75117a78c1A5a0DBb814Af92458539FB4') THEN 'LINK'
					WHEN dp.contract_address = lower('0x82aF49447D8a07e3bd95BD0d56f35241523fBab1') THEN 'WETH'
				END AS "Asset",
				dp."TransactionType"
			FROM
				decrease_position dp
		),
		unique_transactions AS (
			SELECT DISTINCT
				a.TX_HASH,
				a.BLOCK_TIMESTAMP,
				a."Timestamp",
				a."User",
				a."Market",
				a."Amount",
				a."Asset",
				a."TransactionType",
				a."Amount" * tp.PRICE AS "USD_Amount"
			FROM
				amounts a
				JOIN token_prices tp ON a."Timestamp" = tp."Timestamp"
				AND (
					CASE
						WHEN a.contract_address = lower('0xFF970A61A04b1cA14834A43f5dE4533eBDDB5CC8') THEN 'USDC'
						WHEN a.contract_address = lower('0xda10009cbd5d07dd0cecc66161fc93d7c9000da1') THEN 'DAI'
						WHEN a.contract_address = lower('0xFd086bC7CD5C481DCC9C85ebE478A1C0b69FCbb9') THEN 'USDT'
						WHEN a.contract_address = lower('0x17FC002b466eEc40DaE837Fc4bE5c67993ddBd6F') THEN 'FRAX'
						WHEN a.contract_address = lower('0xFa7F8980b0f1E64A2062791cc3b0871572f1F7f0') THEN 'UNI'
						WHEN a.contract_address = lower('0x2f2a2543B76A4166549F7aaB2e75Bef0aefC5B0f') THEN 'WBTC'
						WHEN a.contract_address = lower('0xf97f4df75117a78c1A5a0DBb814Af92458539FB4') THEN 'LINK'
						WHEN a.contract_address = lower('0x82aF49447D8a07e3bd95BD0d56f35241523fBab1') THEN 'WETH'
					END
				) = tp.SYMBOL
			WHERE
				a."User" = lower('${account}')
				AND tp.rn = 1
		),
		unique_transactions_with_cumulative_sum AS (
			SELECT
				*,
				SUM("USD_Amount") OVER (
					PARTITION BY
						"User"
					ORDER BY
						BLOCK_TIMESTAMP ROWS BETWEEN UNBOUNDED PRECEDING
						AND CURRENT ROW
				) AS "Cumulative_USD_Amount"
			FROM
				unique_transactions
		)
	SELECT
		TX_HASH as tx_hash,
		BLOCK_TIMESTAMP as block_timestamp,
		"Timestamp" as chart_timestamp,
		"User" as user,
		"Market" as market,
		"Asset" as asset,
		"Amount" as amount,
		"USD_Amount" as amount_usd,
		"TransactionType" as transaction_type,
		"Cumulative_USD_Amount" as cumulative_amount_usd
	FROM
		unique_transactions_with_cumulative_sum;`,
	`WITH
	increase_position_eth AS (
		SELECT
			CONCAT('https://arbiscan.io/tx/', TX_HASH) AS TX_HASH,
			BLOCK_TIMESTAMP,
			CONCAT(
				EXTRACT(MONTH, BLOCK_TIMESTAMP),
				'-',
				EXTRACT(DAY, BLOCK_TIMESTAMP),
				'-',
				EXTRACT(YEAR, BLOCK_TIMESTAMP)
			) AS "Timestamp",
			ETH_FROM_ADDRESS AS "User",
			'GMX' AS "Market",
			AMOUNT,
			'ETH' as "Asset",
			'Spent' AS "TransactionType",
			- AMOUNT_USD as AMOUNT_USD
		FROM
			arbitrum.core.ez_eth_transfers
		WHERE
			"User" = lower('${account}')
			and (
				eth_to_address = lower('0xb87a436B93fFE9D75c5cFA7bAcFff96430b09868')
				or eth_to_address = lower('0x09f77e8a13de9a35a7231028187e9fd5db8a2acb')
			)
	),
	decrease_position_eth AS (
		SELECT
			CONCAT('https://arbiscan.io/tx/', TX_HASH) AS TX_HASH,
			BLOCK_TIMESTAMP,
			CONCAT(
				EXTRACT(MONTH, BLOCK_TIMESTAMP),
				'-',
				EXTRACT(DAY, BLOCK_TIMESTAMP),
				'-',
				EXTRACT(YEAR, BLOCK_TIMESTAMP)
			) AS "Timestamp",
			ETH_TO_ADDRESS AS "User",
			'GMX' AS "Market",
			AMOUNT,
			'ETH' as "Asset",
			'Received' AS "TransactionType",
			AMOUNT_USD
		FROM
			arbitrum.core.ez_eth_transfers
		WHERE
			"User" = lower('${account}')
			and (
				eth_from_address = lower('0xb87a436B93fFE9D75c5cFA7bAcFff96430b09868')
				or eth_from_address = lower('0x09f77e8a13de9a35a7231028187e9fd5db8a2acb')
				or eth_from_address = lower('0x489ee077994b6658eafa855c308275ead8097c4a')
			)
	),
	combined_transactions AS (
		SELECT
			TX_HASH as tx_hash,
			BLOCK_TIMESTAMP as block_timestamp,
			"Timestamp" as chart_timestamp,
			"User" as user,
			"Market" as market,
			'ETH' as asset,
			AMOUNT as amount,
			AMOUNT_USD as amount_usd,
			"TransactionType" as transaction_type
		FROM
			increase_position_eth
		UNION ALL
		SELECT
			TX_HASH as tx_hash,
			BLOCK_TIMESTAMP as block_timestamp,
			"Timestamp" as chart_timestamp,
			"User" as user,
			"Market" as market,
			'ETH' as asset,
			AMOUNT as amount,
			AMOUNT_USD as amount_usd,
			"TransactionType" as transaction_type
		FROM
			decrease_position_eth
	)
	SELECT
	tx_hash,
	block_timestamp,
	chart_timestamp,
	user,
	market,
	asset,
	amount,
	amount_usd,
	transaction_type,
	SUM(amount_usd) OVER (PARTITION BY user ORDER BY block_timestamp) as cumulative_amount_usd
	FROM
	combined_transactions
	ORDER BY
	block_timestamp;`,
	`WITH
	hegic_transactions AS (
		SELECT
			CONCAT('https://arbiscan.io/tx/', TX_HASH) as TX_HASH,
			BLOCK_TIMESTAMP,
			CONCAT(
				EXTRACT(MONTH, BLOCK_TIMESTAMP),
				'-',
				EXTRACT(DAY, BLOCK_TIMESTAMP),
				'-',
				EXTRACT(YEAR, BLOCK_TIMESTAMP)
			) AS "Timestamp",
			FROM_ADDRESS AS "User",
			- SUM(RAW_AMOUNT / 1e6) AS "Amount",
			'Spent' AS "TransactionType",
			'Hegic' AS "Market"
		FROM
			arbitrum.core.fact_token_transfers
		WHERE
			origin_function_signature = '0x0e5024d7'
			AND contract_address = '0xff970a61a04b1ca14834a43f5de4533ebddb5cc8'
			AND to_address = '0xec096ea6eb9aa5ea689b0cf00882366e92377371'
		GROUP BY
			1,
			2,
			3,
			4
		UNION ALL
		SELECT
			CONCAT('https://arbiscan.io/tx/', TX_HASH) as TX_HASH,
			BLOCK_TIMESTAMP,
			CONCAT(
				EXTRACT(MONTH, BLOCK_TIMESTAMP),
				'-',
				EXTRACT(DAY, BLOCK_TIMESTAMP),
				'-',
				EXTRACT(YEAR, BLOCK_TIMESTAMP)
			) AS "Timestamp",
			TO_ADDRESS AS "User",
			SUM(RAW_AMOUNT / 1e6) AS "Amount",
			'Received' AS "TransactionType",
			'Hegic' AS "Market"
		FROM
			arbitrum.core.fact_token_transfers
		WHERE
			origin_function_signature = '0x914fb518'
			AND contract_address = '0xff970a61a04b1ca14834a43f5de4533ebddb5cc8'
			AND from_address = '0xec096ea6eb9aa5ea689b0cf00882366e92377371'
		GROUP BY
			1,
			2,
			3,
			4
	),
	lyra_transactions AS (
		WITH
			open_position AS (
				SELECT
					CONCAT('https://arbiscan.io/tx/', TX_HASH) as TX_HASH,
					BLOCK_TIMESTAMP,
					CONCAT(
						EXTRACT(MONTH, BLOCK_TIMESTAMP),
						'-',
						EXTRACT(DAY, BLOCK_TIMESTAMP),
						'-',
						EXTRACT(YEAR, BLOCK_TIMESTAMP)
					) AS "Timestamp",
					FROM_ADDRESS AS "User",
					- SUM(RAW_AMOUNT / 1e6) AS "Amount",
					'Spent' AS "TransactionType",
					'Lyra' AS "Market"
				FROM
					arbitrum.core.fact_token_transfers
				WHERE
					origin_function_signature = '0xd6c0bb44'
					AND contract_address = '0xff970a61a04b1ca14834a43f5de4533ebddb5cc8'
				GROUP BY
					1,
					2,
					3,
					4
			),
			close_position AS (
				SELECT
					CONCAT('https://arbiscan.io/tx/', TX_HASH) as TX_HASH,
					BLOCK_TIMESTAMP,
					CONCAT(
						EXTRACT(MONTH, BLOCK_TIMESTAMP),
						'-',
						EXTRACT(DAY, BLOCK_TIMESTAMP),
						'-',
						EXTRACT(YEAR, BLOCK_TIMESTAMP)
					) AS "Timestamp",
					TO_ADDRESS AS "User",
					SUM(RAW_AMOUNT / 1e6) AS "Amount",
					'Received' AS "TransactionType",
					'Lyra' AS "Market"
				FROM
					arbitrum.core.fact_token_transfers
				WHERE
					origin_function_signature = '0x3c8e7624'
					AND contract_address = '0xff970a61a04b1ca14834a43f5de4533ebddb5cc8'
				GROUP BY
					1,
					2,
					3,
					4
			),
			force_close_and_settle AS (
				SELECT
					CONCAT('https://arbiscan.io/tx/', TX_HASH) as TX_HASH,
					BLOCK_TIMESTAMP,
					CONCAT(
						EXTRACT(MONTH, BLOCK_TIMESTAMP),
						'-',
						EXTRACT(DAY, BLOCK_TIMESTAMP),
						'-',
						EXTRACT(YEAR, BLOCK_TIMESTAMP)
					) AS "Timestamp",
					TO_ADDRESS AS "User",
					SUM(RAW_AMOUNT / 1e6) AS "Amount",
					'Received' AS "TransactionType",
					'Lyra' AS "Market"
				FROM
					arbitrum.core.fact_token_transfers
				WHERE
					origin_function_signature IN (
						'0xad884d6b',
						'0x778bd3d3',
						'0xf8866820',
						'0x5bf3e977',
						'0x56f08312',
						'0xc4e521d5',
						'0x1d5565f0'
					)
					AND contract_address = '0xff970a61a04b1ca14834a43f5de4533ebddb5cc8'
				GROUP BY
					1,
					2,
					3,
					4
			)
		SELECT
			*
		FROM
			open_position
		UNION ALL
		SELECT
			*
		FROM
			close_position
		UNION ALL
		SELECT
			*
		FROM
			force_close_and_settle
	),
	transactions_combined AS (
		SELECT
			*
		FROM
			hegic_transactions
		UNION ALL
		SELECT
			*
		FROM
			lyra_transactions
		ORDER BY
			"Timestamp" ASC,
			"User"
	)
	SELECT
	TX_HASH as tx_hash,
	BLOCK_TIMESTAMP as block_timestamp,
	"Timestamp" as chart_timestamp,
	"User" as user,
	"Market" as market,
	'USDC' as asset,
	"Amount" as amount,
	"Amount" as amount_usd,
	"TransactionType" as transaction_type,
	SUM("Amount") OVER (
		PARTITION BY
			"User"
		ORDER BY
			BLOCK_TIMESTAMP
	) as cumulative_amount_usd
	FROM
	transactions_combined
	where
	"User" = lower('${account}')`,
	`WITH
	combined_transactions AS (
		-- openTrade event
		SELECT
			CONCAT('https://arbiscan.io/tx/', TX_HASH) as TX_HASH,
			BLOCK_TIMESTAMP,
			CONCAT(
				EXTRACT(MONTH, BLOCK_TIMESTAMP),
				'-',
				EXTRACT(DAY, BLOCK_TIMESTAMP),
				'-',
				EXTRACT(YEAR, BLOCK_TIMESTAMP)
			) AS "Timestamp",
			FROM_ADDRESS as "User",
			- SUM(RAW_AMOUNT / 1e18) as "Amount",
			'GNS' AS "Market",
			'Spent' AS "TransactionType"
		FROM
			arbitrum.core.fact_token_transfers
		WHERE
			arbitrum.core.fact_token_transfers.origin_function_signature = '0x9aa7c0e5'
			AND arbitrum.core.fact_token_transfers.contract_address = lower('0xda10009cbd5d07dd0cecc66161fc93d7c9000da1')
			AND to_address = lower('0xcfa6ebd475d89db04cad5a756fff1cb2bc5be33c')
		GROUP BY
			1,
			2,
			3,
			4
		UNION ALL
		-- fulfillOracleRequest event
		SELECT
			CONCAT('https://arbiscan.io/tx/', TX_HASH) as TX_HASH,
			BLOCK_TIMESTAMP,
			CONCAT(
				EXTRACT(MONTH, BLOCK_TIMESTAMP),
				'-',
				EXTRACT(DAY, BLOCK_TIMESTAMP),
				'-',
				EXTRACT(YEAR, BLOCK_TIMESTAMP)
			) AS "Timestamp",
			TO_ADDRESS as "User",
			SUM(RAW_AMOUNT / 1e18) as "Amount",
			'GNS' AS "Market",
			'Received' AS "TransactionType"
		FROM
			arbitrum.core.fact_token_transfers
		WHERE
			arbitrum.core.fact_token_transfers.origin_function_signature = '0x4ab0d190'
			AND arbitrum.core.fact_token_transfers.contract_address = lower('0xda10009cbd5d07dd0cecc66161fc93d7c9000da1')
			AND from_address = lower('0xcfa6ebd475d89db04cad5a756fff1cb2bc5be33c')
		GROUP BY
			1,
			2,
			3,
			4
	)
	SELECT
	TX_HASH as tx_hash,
	BLOCK_TIMESTAMP as block_timestamp,
	"Timestamp" as chart_timestamp,
	"User" as user,
	"Market" as market,
	'DAI' as asset,
	"Amount" as amount,
	"Amount" as amount_usd,
	"TransactionType" as transaction_type,
	SUM("Amount") OVER (
		PARTITION BY "User"
		ORDER BY BLOCK_TIMESTAMP
	) as cumulative_amount_usd
	FROM
	combined_transactions
	where
	"User" = lower('${account}')
	ORDER BY
	"User",
	BLOCK_TIMESTAMP;`
	];
}
